<template>
    <div class="table-list-page">
        <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :recordCount="recordCount" :disCodeOption="disCodeOption"  @onAdd="handleAdd"
            @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit" @onTreeNode="handleTreeNode">
            <div slot="header">
                <el-row type="flex" justify="space-around" class="techo-list__header-row">                 
                    <el-col :span="10">
                        <span class="techo-list__header-col-txt-3">学校名称：</span>
                        <div class="techo-list__header-col-left-3">
                          <el-input placeholder="请输入学校名称" :maxlength="10" size="small" class="techo-list__header-select-full"
                          filterable change-on-select clearable 
                          v-model="queryForm.name">
                          </el-input>
                        </div>
                    </el-col>
                    <el-col :span="9">
                    </el-col>
                    <el-col :span="5" class="techo-list__header-col-button">
                        <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
                        <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
                    </el-col>
                </el-row>
            </div>
        </techo-list>
        
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey"></techo-details>
        <vAddDialog ref="vAddDialog"></vAddDialog>
    </div>
</template>

<script>
import service from "./services/campus_security_service.js";
import vAddDialog from "./campus-security-add.vue";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "学校安全",
      header: {
        学校名称: "name",
        学校地址: "adress",
        学校办学类型: "dicMap.type",
        //学校所属主管教育行政部门: "dept",
        //在校学生人数: "studentsCount",
        校长姓名: "rectorName",
        校长联系方式: "rectorContact",
        //分管安全保卫责任人姓名: "partSecurityName",
        //分管安全保卫责任人联系方式: "partSecurityContact",
        //安全保卫责任人姓名: "responsibleName",
        //安全保卫责任人联系方式: "responsibleContact",
        //治安责任人姓名: "securityName",
        //治安责任人联系方式: "securityContact",
        治安保卫人数: "guardsCount"
      },
      data: [],
      recordCount: 0,
      queryForm: {
        districtCode: "",
        name: ""
      },
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "schoolInfo",
      disCodeOption: []
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
  },
  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加  */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      this.$refs["vAddDialog"].dialogForm.districtCode = this.queryForm.districtCode;
    },
    /** 删除   */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteCampusSecurity(item.id, data => {
            index == tableSelection.length - 1 && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "信息删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "信息删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      this.$refs["vAddDialog"].dialogForm = _.cloneDeep(row);
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        name: ""
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo = { ...pageInfo, ..._.cloneDeep(this.queryForm) };
      service.requestPageCampusSecurity(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageCampusSecurity(pageInfo, data => {
            this.recordCount = data.totalCount;
            _.map(data.list, item => {
              item.dicMap &&
                (item.dicMap.isHaveSub =
                  item.peoleCount !== 0 || item.caseCount !== 0 ? true : false);
            });
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          _.map(data.list, item => {
            item.dicMap &&
              (item.dicMap.isHaveSub =
                item.peoleCount !== 0 || item.caseCount !== 0 ? true : false);
          });
          this.data = data.list;
        }
      });
    }
  }
};
</script>
