<template>
  <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen>
    <el-tabs v-model="activeName">
        <el-tab-pane label="学校信息" name="first">
          <el-form ref="schForm" :model="schForm" :rules="fristRules" label-width="120px"> 
              <el-row :gutter="15">
                <el-col :span="8">
                  <el-form-item label="学校名称" prop="name">
                    <el-input v-model="schForm.name" placeholder="请填写学校名称"></el-input>
                  </el-form-item>
                  <el-form-item label="学校办学类型" prop="type">
                      <el-select placeholder="请选择学校办学类型" clearable v-model="schForm.type">
                          <el-option v-for="(item, index) in dic['学校办学类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                      </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="学校地址" prop="adress">
                    <el-input v-model="schForm.adress" placeholder="请填写学校地址"></el-input>
                  </el-form-item>
                  <el-form-item label="在校学生人数" prop="studentsCount">
                    <el-input v-model="schForm.studentsCount" :maxlength="6" placeholder="请填写在校学生人数"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">         
                  <el-form-item label="治安保卫人数" prop="guardsCount">
                    <el-input v-model="schForm.guardsCount"  placeholder="请填写治安保卫人数"></el-input>
                  </el-form-item>    
                  <el-form-item label="学校所属主管教育行政部门" prop="dept" label-width="200px">
                      <el-select placeholder="请选择学校所属主管教育行政部门" clearable v-model="schForm.dept">
                          <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>                          
                      </el-select>
                  </el-form-item>    
                </el-col>
            </el-row>
          </el-form>
        </el-tab-pane>
      <el-tab-pane label="责任人信息" name="second"> 
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">       
          <el-row :gutter="15">        
            <el-col :span="12">
              <el-form-item label="校长姓名" prop="rectorName" label-width="200px">
                <el-input v-model="dialogForm.rectorName"  placeholder="请填写校长姓名"></el-input>
              </el-form-item>
              <el-form-item label="治安责任人姓名" prop="securityName" label-width="200px">
                <el-input v-model="dialogForm.securityName"  placeholder="请填写治安责任人姓名"></el-input>
              </el-form-item>
              <el-form-item label="安全保卫责任人姓名" prop="responsibleName" label-width="200px">
                <el-input v-model="dialogForm.responsibleName"  placeholder="请填写安全保卫责任人姓名"></el-input>
              </el-form-item>
              <el-form-item label="分管安全保卫责任人姓名" prop="partSecurityName" label-width="200px">
                <el-input v-model="dialogForm.partSecurityName"  placeholder="请填写分管安全保卫责任人姓名"></el-input>
              </el-form-item>            
            </el-col>
            <el-col :span="12">
              <el-form-item label="校长联系方式" prop="rectorContact" label-width="200px">
                <el-input v-model="dialogForm.rectorContact"  placeholder="请填写校长联系方式"></el-input>
              </el-form-item>
              <el-form-item label="治安责任人联系方式" prop="securityContact" label-width="200px">
                  <el-input v-model="dialogForm.securityContact"  placeholder="请填写治安责任人联系方式"></el-input>
              </el-form-item>
              <el-form-item label="安全保卫责任人联系方式" prop="responsibleContact" label-width="200px">
                <el-input v-model="dialogForm.responsibleContact"  placeholder="请填写安全保卫责任人联系方式"></el-input>
              </el-form-item>
              <el-form-item label="分管安全保卫责任人联系方式" prop="partSecurityContact" label-width="210px">
                <el-input v-model="dialogForm.partSecurityContact"  placeholder="请填写分管安全保卫责任人联系方式"></el-input>
              </el-form-item>
            </el-col>
          </el-row>              
        </el-form>
      </el-tab-pane>
  </el-tabs>
    <div slot="footer" class="dialog-footer">
      <el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
			<el-button v-if="activeName == 'second'" type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
            <el-button v-if="activeName == 'second'" type="success" size="small" @click="submitForm">保存</el-button>
		</div>
  </el-dialog>
</template>
<script>
import service from "./services/campus_security_service.js";
export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      activeName: "first",
      dialogImageUrl: '',
      dic: {
        学校办学类型: [],
        行政区划 : []
      },
      disCodeOption: [],
      schForm:{
        name: "",
        adress: "",
        type: "",
        dept: "",
        studentsCount: "",
        guardsCount: "",
      },
      dialogForm: {
        name: "",
        rectorName: "",
        rectorContact: "",
        partSecurityName: "",
        partSecurityContact: "",
        responsibleName: "",
        responsibleContact: "",
        securityName: "",
        securityContact: "",
        districtCode: ""
      },
      fristRules:{
        name: [
          { required: true, message: "请输入学校名称", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 10 个字符", trigger: "blur" }
        ],
        adress: [
          { required: true, message: "请输入学校地址", trigger: "blur" },
          { min: 2, max: 25, message: "长度在 2 到 25 个字符", trigger: "blur" }
        ],

        type: [
          { required: true, message: "请选择学校办学类型", trigger: "change" }
        ],
        dept: [
          {
            message: "请选择学校所属主管教育行政部门", trigger: "change" }
        ],
        studentsCount: [
          {message: "请输入在校学生人数", trigger: "blur" },
          util.validate.valiInteger()
        ],
        guardsCount: [
          { required: true, message: "请输入治安保卫人数", trigger: "blur" },
          util.validate.valiInteger()
        ],
      },
      teamRules: {
        rectorName: [
          { required: true, message: "请输入校长姓名", trigger: "blur" },
          { min: 2, max: 5, message: "长度在 2 到 20 个字符", trigger: "blur" }
        ],
        rectorContact: [
          { required: true, message: "请输入校长联系方式", trigger: "blur" },
          util.validate.tellAndPhone()
        ],
        partSecurityName: [
          { min: 2, max: 5, message: "长度在 2 到 20 个字符", trigger: "blur" }
        ],
        partSecurityContact: [
          util.validate.tellAndPhone()
        ],
        responsibleName: [
          { min: 2, max: 5, message: "长度在 2 到 20 个字符", trigger: "blur" }
        ],
        responsibleContact: [
          util.validate.tellAndPhone()
        ],
        securityName: [
          { min: 2, max: 5, message: "长度在 2 到 20 个字符", trigger: "blur" }
        ],
        securityContact: [
          util.validate.tellAndPhone()
        ],
        
        
      }
    };
  },

  watch: {
    activeName(name) {
      if (name == "second") {
        this.$refs["schForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
      }
    }
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },
  methods: {
    
    /** 保存 */
    submitForm() {
        this.$refs["dialogForm"].validate(valid => {
          if (valid) {
            let param = _.cloneDeep(this.dialogForm);
            param.dicMap && delete param.dicMap;
            if (this.isAdd == true) {
              // 添加
              service.requestSaveCampusSecurity(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "信息添加成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            } else if (this.isAdd == false) {
              // 修改
              service.requestUpdateCampusSecurity(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "信息修改成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            }
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.activeName = "first";
      this.dialogForm = {        
        districtCode: ""
      };
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-dialog .el-select {
  width: 100%;
}
.add-dialog .el-cascader {
  width: 100%;
}
</style>